.homepageWrapper {
  height: 100vh;
  width: 100vw;
  padding-left: 25vw;
}

.section {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: flex-start;
  align-items: center;
  font-family: 'Open Sans', sans-serif;
  color: #D1DECE;
  min-height: 100vh;
  width: 100%;
  /* border: 1px solid black; */
}

.sectionLabel {
  font-family: 'Lato', sans-serif;
  color: white;
  font-size: 5vh;
  font-weight: 200;
  text-align: center;
  padding-top: 8vh;
  padding-bottom: 3vh;
  border-bottom: 2px solid seagreen;
}

.about {
  background-color: transparent;
  background-image: url(../../assets/bythesea.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.skills {
  background-color:#85B3B0;
}
.projects {
  background-color:#0E5C5A;
}
.resume {
  background-color:#143D3E;
}
.contact {
  background-color:#0b2020;
}
